body {
    line-height: 1;
    /*background-color: #FF9A8B;*/
    /*background-image: linear-gradient(90deg, #FF9A8B 0%, #FF6A88 55%, #FF99AC 100%);*/
    background-color: black;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 5555;
}

.overlay .close-button {
    position: absolute;
    top: 15px;
    right: 15px;
    width: 5rem;
    height: 5rem;
    cursor: pointer;
}

.overlay .line {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    margin: auto;
    width: 80%;
    height: 0.2rem;
    background-color: red; /* Adjust the color as needed */
}

.overlay .line1 {
    transform: rotate(45deg);
}

.overlay .line2 {
    transform: rotate(-45deg);
}

.overlay img {
    max-width: 100%;
    max-height: 100%;
}
